import { Link } from 'react-router-dom'

import FavoriteIcon from '@mui/icons-material/Favorite'
import { Box, Card, CardActionArea, Chip, Stack, Typography, useMediaQuery, useTheme } from '@mui/material'

import { type ClubBelonging } from 'src/data/belonging'

import { stringToColor } from 'src/utils'

export interface RecruitNoticePreviewProps {
  id: number
  clubId: number
  // year: number
  // semester: string
  title: string
  expireIn: number
  club: RecruitClub
}

interface RecruitClub {
  name: string
  category: string
  belonging: ClubBelonging
  likeCount: number
}

const RecruitNoticePreview = ({ clubId, title, expireIn, club }: RecruitNoticePreviewProps) => {
  const theme = useTheme()
  const isNotMobile = useMediaQuery(theme.breakpoints.up('sm'))
  const dDay = expireIn / 86400000

  return (
    <Card>
      <CardActionArea
        component={Link}
        to={`/club/${clubId}`}
        sx={{ display: 'flex', alignItems: 'stretch', padding: theme.spacing(2), minHeight: '12.8em' }}
      >
        <Stack direction="column" flex="1" spacing={1}>
          <Stack direction="row" spacing={1}>
            <Chip
              label={club.category}
              size="small"
              sx={{ padding: theme.spacing(0.5), fontSize: isNotMobile ? 13 : 11 }}
            />
            {club.belonging && (
              <Chip
                label={club.belonging}
                size="small"
                sx={{ padding: theme.spacing(0.5), fontSize: isNotMobile ? 13 : 11 }}
              />
            )}
          </Stack>
          <Stack direction="row" spacing={1} alignItems="center" paddingLeft={0.5} paddingTop={0.5}>
            {/* <Box sx={{ backgroundColor: '#008989' }} width="28px" height="28px" borderRadius="50%" /> */}
            <Typography variant="h4">{club.name}</Typography>
          </Stack>
          <Typography
            paddingLeft={0.4}
            paddingTop={0.2}
            paddingRight={1.5}
            variant="body2"
            fontSize={15}
            flex={1}
            overflow="hidden"
            textOverflow="ellipsis"
            sx={{
              wordBreak: 'keep-all',
              display: '-webkit-box',
              WebkitLineClamp: '2',
              WebkitBoxOrient: 'vertical'
            }}
          >
            {title}
          </Typography>
          <Stack direction="row" alignItems="center" paddingLeft={0.5}>
            <Typography variant="body2" fontSize={15} color={dDay == 0 ? theme.colors.alpha.black[50] : theme.colors.primary.main} fontWeight="bold">
              {dDay == 0 ? '모집 종료' : dDay < 1 ? 'D-Day' : `D-${Math.floor(dDay)}`}
            </Typography>

            <FavoriteIcon
              sx={{
                width: '0.6em',
                height: '0.6em',
                marginLeft: theme.spacing(1),
                color: theme.colors.alpha.black[30]
              }}
            />
            <Typography variant="body2" fontSize={15} color={theme.colors.alpha.black[30]} marginLeft={0.2}>
              {club.likeCount}
            </Typography>
          </Stack>
        </Stack>
        <Box display="flex" alignItems="center" width="130px">
          <Box width="130px" height="130px" sx={{ backgroundColor: stringToColor(club.name, 0.3) }} />
        </Box>
      </CardActionArea>
    </Card>
  )
}

export default RecruitNoticePreview
